.Home-title {
	font-size: 2em;
	margin-left: 5%;
	margin-top: 10px;
}

.Home-games-container {
	margin-top: 10px;
}

.Home-games-container-hidden {
	display: none;
}

.Home-games-header-separator {
	margin-top: 15px;
	height: 1px;
	width: 90%;
	margin-left: auto;
	margin-right: auto;
	background-color: #DDDDDD;
}

.Home-finished-games-header-separator {
	height: 1px;
	width: 90%;
	margin-left: auto;
	margin-right: auto;
	background-color: #DDDDDD;
}

.Home-header-tooltip {
	vertical-align: top;
	margin-top: 5px;
}

.Home-game-state-tabs-container {
	margin-left: 5%;
}

/* Margin between tabs */
.Home-game-state-tab + .Home-game-state-tab{
	margin-left: 5px;
}

.Home-game-state-tab.Home-game-state-tab-selected {
	border: none;
	border-radius: 0px;
	border-bottom: 4px solid green;
	-webkit-transition: border-bottom 1s;
	-o-transition: border-bottom 1s;
	transition: border-bottom 1s;
	background: none;
}

.Home-game-state-tab {
	border: none;
	border-radius: 0px;
	border-bottom: 4px solid #DDDDDD;
	background: none;
}

.Home-game-state-tab:hover {
	background: #DDDDDD;
	-webkit-transition: background 0.3s;
	-o-transition: background 0.3s;
	transition: background 0.3s;
}

.Home-finished-games-header {
	width: 90%;
	margin-left: auto;
	margin-right: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.Home-review-explanation-message {
	width: 90%;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 10px;
}

.Home-finished-games-status-header {
	width: 5%;
}
.Home-finished-games-game-name-header {
	width: 25%;
}
.Home-finished-games-link-header {
	width: 7%;
}
.Home-finished-games-winning-outcome-header {
	width: 10%;
}
.Home-finished-games-stake-header {
	width: 5%;
}
.Home-finished-games-winnings-header {
	width: 5%;
}
.Home-finished-games-claim-button-header {
	width: 10%;
}


