.ClosedGameCard {
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 90%;
    margin-bottom: 15px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 5%;
    background-color: #f9f9f9;
}

.ClosedGameCard-game-name {
	text-align: center;
	padding-top: 10px;
    font-size: 17pt;
}

.ClosedGameCard-game-state {
	text-align: center;
}

.ClosedGameCard-link-and-proposition {
    text-align: center;
}

.ClosedGameCard-gameLink {
    margin-right: 10px;
}

.ClosedGameCard-total-pool-amount {
	text-align: center;
    font-size: 16pt;
}

.ClosedGameCard-betting-table {
	border-collapse:separate; 
	border-spacing: 6px;
	text-align: center;
}

.ClosedGameCard-separator {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    height: 1px;
    margin-left: auto; 
    margin-right: auto;
    background-color: grey;
}


.ClosedGameCard-betting-table-header {
    font-size: 15px;
    text-decoration: underline;
	width: 5%;
	text-align: center;
}

.ClosedGameCard-betting-table-row {
    font-size: 20px;    
}



