.ResolvedGameCard {
    width: 90%;
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    background-color: #f9f9f9;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    border-bottom: 1px solid #DDDDDD;
}

.ResolvedGameCard-row-element {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.ResolvedGameCard-game-state {
    width: 5%;
}

.ResolvedGameCard-game-name {
    width: 25%;
}

.ResolvedGameCard-game-link {
    width: 7%;   
}

.ResolvedGameCard-winning-outcome {
    width: 10%;
}

.ResolvedGameCard-user-stake {
    width: 5%;   
}

.ResolvedGameCard-user-winnings {
    width: 5%;   
}

.ResolvedGameCard-claim-button-container {
    width: 10%;   
}

.ResolvedGameCard-claim-button {
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    height: 30px;
    width: 100px;
    font-size: 10pt;
}

.ResolvedGameCard-claim-button:hover {
    background-color: #349929; /* Green */
    color: #efefef;
}




