.OpenGameCard {
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 90%;
    margin-bottom: 15px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 5%;
    background-color: #f9f9f9;
}

.OpenGameCard-game-name {
	text-align: center;
	padding-top: 10px;
    font-size: 17pt;
}

.OpenGameCard-timer {
    text-align: center;
}

.OpenGameCard-link-and-proposition {
    text-align: center;
}

.OpenGameCard-gameLink {
    margin-right: 10px;
}

.OpenGameCard-game-state {
	text-align: center;
}

.OpenGameCard-total-pool-amount {
	text-align: center;
    font-size: 16pt;
}

.OpenGameCard-bet-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.OpenGameCard-bet-form-input {
	width: 40%;
}

.OpenGameCard-bet-form-input-disabled {
    width: 40%;
    pointer-events: none;
    background-color: #DDDDDD;
}

.OpenGameCard-currency-symbol span {
    vertical-align: middle;
}

.OpenGameCard-betting-table {
	border-collapse:separate; 
	border-spacing: 6px;
	text-align: center;
}

.OpenGameCard-separator {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    height: 1px;
    margin-left: auto; 
    margin-right: auto;
    background-color: grey;
}


.OpenGameCard-betting-table-header {
    font-size: 15px;
    text-decoration: underline;
	width: 5%;
	text-align: center;
}

.OpenGameCard-betting-table-row {
    font-size: 20px;    
}

.OpenGameCard-bet-button {
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    border-radius: 0px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 10px;
    background-color: #4CAF50;
    color: white;
}

.OpenGameCard-bet-button-disabled {
    border-radius: 0px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 10px;
    pointer-events: none;
}

.OpenGameCard-bet-button:hover {
    background-color: #349929; /* Green */
    color: #efefef;
}



