.IntroSection-container {
	height: 200px;
}

.IntroSection-x-button-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
}

.IntroSection-x-button {
	-webkit-transform: scale(1.5);
	    -ms-transform: scale(1.5);
	        transform: scale(1.5);
	margin: 15px 15px 0px 0px;
}

.IntroSection-text-container {
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
 	-ms-flex-align: center;
 	    align-items: center;
 	-ms-flex-pack: center;
 	    justify-content: center;
}

.IntroSection-text-element {
	margin-top: 10px;
}

.IntroSection-text {
	font-size: 18px;
}

.IntroSection-text-header {
	font-size: 30px;
}

.IntroSection-button {
	margin-top: 20px;
	font-size: 20px;
	border-radius: 1px;
	-webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
	   transition-duration: 0.4s;
    background-color: #ffffff;
    border-color: #4CAF50;
    color: #4CAF50;
}

.IntroSection-button:hover {
    background-color: #4CAF50; /* Green */
    color: #ffffff;
}




