ul.Homebar-topnav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

ul.Homebar-topnav li {float: left;}

ul.Homebar-topnav li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

ul.Homebar-topnav li a:hover:not(.Homebar-active) {background-color: #111;}

ul.Homebar-topnav li a.Homebar-active {background-color: #4CAF50;}

ul.Homebar-topnav li.Homebar-right {float: right;}

@media screen and (max-width: 600px){
    ul.Homebar-topnav li.Homebar-right,
    ul.Homebar-topnav li {float: none;}
}

.Homebar-sub-navbar {
	width: 100%;
	height: 30px;
    min-width: 780px;
	background-color: #394156;
    border-color: #e7e7e7;
    border-bottom-width: 1px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;

}

.Homebar-sub-navbar-left {
	display: -ms-flexbox;
	display: flex;
}

.Homebar-sub-navbar-left-item {
	margin-left: 10px;
	margin-right: 10px;
	margin-bottom: auto;
	margin-top: auto;
	font-size: 14px;
}

.Homebar-learn-more-link > a {
    color: #6da5ff;
}

.Homebar-sub-navbar-right-item {
	margin-left: 10px;
	margin-right: 15px;
	font-size: 18px;
	cursor: pointer;
}

.Homebar-enabled-provider-button {
    background-color: transparent;
    height: 20px;
    border: 0;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: white;
    text-decoration: underline;
}

.Homebar-enabled-provider-button:hover {
    color: #DDDDDD;
    -webkit-transition: color 0.3s;
    -o-transition: color 0.3s;
    transition: color 0.3s;
}

.Homebar-dropdown-menu:focus {
    background-color: #394156;
    color: white;
}

.Homebar-dropdown-menu {
    background-color: #394156;
    border: 0;
    border-radius: 0px;
    height: 30px;
    color: white;
    margin-right: 10px;
}

.Homebar-dropdown-menu:hover {
    background-color: #2b3142;
    color: #DDDDDD;
    -webkit-transition: background-color 0.3s, color 0.3s;
    -o-transition: background-color 0.3s, color 0.3s;
    transition: background-color 0.3s, color 0.3s;
}

.Homebar-settings-button {
	vertical-align: middle;
}

.Homebar-item-white {
	color: #efefef;
}

.Homebar-modal-title {
    display: inline-block;
    margin-right: 5px;
}


