.StatusBox {
    position: fixed;
    bottom: 0;
    right: 0;
    border: 3px solid #000000;
}

.StatusBox-header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	color: white;	
	background-color: black;
	height: 10%;
	width: 250px;
}

.StatusBox-body {
	background-color: white;
	width: 250px;
    height: 250px;
    overflow-y: scroll;
}

.StatusBox-header-content {
	position: relative;
	top: 50%;
	margin-left: 7px;
	display: inline-block;
}

.StatusBox-hidden {
	display:none;
}

.StatusBox-minimize-button-container {
	max-height: 100%;
	max-width: 100%;
	float: right;
}

.StatusBox-minimize-button {
	display: inline-block;
	float: right;
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
	margin-right: 10px;
	height: 5px;
	width: 15px;
	background-color: #FFFFFF;
}

.StatusBox-transaction {
	height: 70px;
	width: 100%;
	background-color: #F7F7F7;
	border-bottom: 2px dotted black;
}

.StatusBox-transaction-content {
	margin-left: 5px;
	font-size: 13px;
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.StatusBox-transaction-icon {
	height: 30px;
	width: auto;
	margin-right: 7px;
}


